.Pokecard {
  border-style: solid;
  border-color: rgb(254, 224, 102);
  border-width: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: white;
  border-radius: 10px;
  
  .Pokecard-img {
    width: 93%;
    // background-color: rgba(185, 180, 180, 0.795);
    background-image: url(https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/3c938f85-f834-4bb3-b3b2-97d295769464/d7a1rke-db613c16-7069-48e5-8cbd-37a5dbf65c49.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..Hr4gKXosyOcRLxod-DiIXXeWm5ZsizTmfmlYa7t9-Xc);
    text-align: center;
    
    img {
      transition: all ease 500ms;
    }
  }
}


img:hover {
	transform: scale(1.2);
}